/* STATIC STYLES (desktop-first) */

.tilt-card1 {
  padding: 1px;
  border-radius: 2rem;
  background: linear-gradient(135deg, #6EE7B7, #F9A8D4);
  box-shadow: 0px 35px 120px -15px var(--color-card);
  transform-style: preserve-3d;
  will-change: transform;
}

.tilt-card1-inner {
  background-color: var(--color-primary);
  border-radius: 2rem;
  padding: 3rem;
  height: 100%;
  will-change: transform;
}

