/* STATIC STYLES (desktop-first) */

.services {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}

.services-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 4rem;
}

.services-card {
  place-items: center;
}

.services-card h2 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  line-height: 1.1;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {

}

@media (max-width: 768px) {

}

@media (max-width: 576px) {
  .services-cards {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    gap: 2rem;
  }

  .services-card h2 {
    font-size: 1.1rem;
  }

  .services-card p {
    font-size: .9rem;
  }
}